import styled from "styled-components";
import React, { useEffect, useState } from "react";
import { GoogleMap, LoadScript, Marker } from "@react-google-maps/api";
import { useParams } from "react-router-dom";
import { geocode, getTheatreById } from "../data/data";
import images from "../data/images";

const TheatreDetail = (props) => {
  const [position, setPosition] = useState(null);
  const [theatre, setTheatre] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    getTheatreById({ id: id })
      .then((data) => {
        setTheatre(data);
      })
      .catch((error) => {
        console.error(error);
      });
    console.log(theatre.address);

    const fetchCoordinates = async () => {
      const coordinates = await geocode(theatre.address);
      setPosition(coordinates);
    };
    fetchCoordinates();
    console.log(position);
  }, [id]);

  const containerStyle = {
    width: "100%",
    height: "400px",
  };

  return (
    <Container>
      <TheatreTitle>
        <h1>H Thng Rp</h1>
      </TheatreTitle>
      <TheatreContainer>
        <TheatreContent>
          <h1>{theatre.name}</h1>
          <ul>
            <li>a im: {theatre.address}</li>
            <li>S in thoi: 1900 2099 hoc {theatre.phone_number}</li>
            <li>Email: {theatre.email}</li>
            <li>Phng chiu: {theatre.description}</li>
          </ul>
          <img src={images.priceBoard}></img>
          <h1>CC QUY NH GI V</h1>
          <ul>
            <li>
              Gi v tr em p dng cho tr em c chiu cao di 1,3m. Yu cu
              tr em c mt khi mua v. Tr em di 0,7m s c min ph v khi
              mua cng 01 v ngi ln i km theo. Khng p dng km vi chng
              trnh khuyn mi u i v gi v khc.
            </li>
            <li>
              Gi v thnh vin U22 ch p dng cho thnh vin di 22 tui khi
              mua v. Khng p dng km vi chng trnh khuyn mi u i v
              gi v khc. Mi th thnh vin U22 c p dng gi v u i ti
              a 02 v/ngy.
            </li>
            <li>
              Ngy l: 1/1, Gi T Hng Vng 10/3 m Lch, 30/4, 1/5, 02 ngy
              L Quc Khnh
            </li>
            <li>Gi v Tt m Lch s c p dng ring.</li>
            <li>
              Sut chiu c bit p dng gi v theo khung gi ca ngy. Khng
              p dng cc gi v u i dnh cho U22, Privilege Voucher/Staff
              Voucher, Happy Day. Trong trng hp Sut chiu c bit cng ngy
              vi Happy Day s p dng gi v ca Th 3
            </li>
          </ul>
          <Map>
            <LoadScript googleMapsApiKey="AIzaSyBA-wMiHOMAOZuB8ngN1xv0p-9dViRnVeA">
              {position && (
                <GoogleMap
                  mapContainerStyle={containerStyle}
                  center={position}
                  zoom={20}
                >
                  <Marker position={position} />
                </GoogleMap>
              )}
            </LoadScript>
          </Map>
        </TheatreContent>
      </TheatreContainer>
    </Container>
  );
};

const Container = styled.div`
  position: relative;
  width: 100vw;
  min-height: calc(100vh - 250px);
  height: auto;
  overflow-x: hidden;
  display: block;
  top: 72px;
  padding: 0 calc(3.5vw + 5px);

  &:after {
    background: url("/images/home-background.png") center center/ cover
      no-repeat fixed;
    content: "";
    position: absolute;
    inset: 0px;
    opacity: 1;
    z-index: -1;
  }
`;

const TheatreTitle = styled.div`
  padding: 40px 0px;
  min-height: auto;
  color: #fff;

  h1 {
    text-transform: uppercase;
    margin: 0px;
    font-size: 1.6rem;
    text-align: center;
  }
`;

const TheatreContainer = styled.main`
  position: relative;
  display: block;
`;

const TheatreContent = styled.div`
  max-width: 1100px;
  min-width: 390px;
  height: auto;
  border: 1px solid #454d6a;
  border-radius: 5px;
  margin: 0 auto;
  text-align: left;
  padding: 0 20px 0 20px;
  color: #fff;

  ul {
    margin-bottom: 1.3rem;
    list-style: disc;
    margin-top: 0;
    margin-left: 20px;
    padding: 0;

    li {
      margin-top: 10px;
    }
  }

  img {
    display: inline-block;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
  }
`;

const Map = styled.div`
  margin-bottom: 25px;
`;

export default TheatreDetail;
